import { NextContext } from 'next';

function TaiPeiCard({ stars }: any) {
  return <div>Next stars: {stars}</div>
}

type QueryString = {
  userDID: string
};

TaiPeiCard.getInitialProps = async ({ query }: NextContext<QueryString>) => {
  return { userDID: query.userDID };
}

export default TaiPeiCard;